/* ====================== Restaurant Filtering ====================== */
.filter-options {
    width: 100%;
    height: 50px;
    background-color: #3397DB;
    align-items: center;
    display: flex;
    align-items: center;
    justify-content: center;

    h2 {
      color: white;
      font-size: 1rem;
      font-weight: normal;
      line-height: 1;
      margin: 0 20px;
    }

    select {
      background-color: white;
      border: 1px solid #fff;
      font-family: Arial,sans-serif;
      font-size: 11pt;
      height: 35px;
      letter-spacing: 0;
      margin: 10px;
      padding: 0 10px;
      width: 200px;
    }
  }